  -moz-osx-font-smoothing: grayscale;
}
*,
*::after,
*::before {
  margin: 0px;
  padding: 0px;
}

.container {
  margin: 1rem 3rem;
}


.admin-nav-container {
  max-width: 100% !important;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px 30px;
  font-size: 16px;
}

.admin-links {
  list-style-type: none;
  display: flex;
  flex-direction: row;
}

.admin-links li:not(:first-child) {
  margin-left: 25px;
}

a {
  text-decoration: none;
}
.nav-link {
  color: rgba(50, 99, 131, 1);
}

.nav-link:hover,
.nav-link.active {
  color: #114be0;
}
.bar {
  height: 3px;
  background-color: #000;
  width: 20px;
  position: relative;
}
.bars:hover > .bar {
  background-color: #114be0;
}

.bars:hover > .bar::after {
  background-color: #114be0;
}
.bars:hover > .bar::before {
  background-color: #114be0;
}
.bars {
  cursor: pointer;
  height: 20px;
}
.bar::after {
  content: '';
  width: 20px;
  height: 3px;
  background-color: #000;
  position: absolute;
  bottom: 10px;
}
.bar::before {
  content: '';
  width: 20px;
  height: 3px;
  background-color: #000;
  position: absolute;
  top: 10px;
}
.bars {
  display: none;
}
.times {
  cursor: pointer;
  height: 20px;
  display: none;
  z-index: 100;
  position: absolute;
  top: 20px;
  right: 20px;
  transform: rotate(-30deg);
}
.time {
  height: 3px;
  background-color: #fff;
  width: 20px;
  position: relative;
  transform: rotate(80deg);
}
.time::after {
  content: '';
  width: 20px;
  height: 3px;
  background-color: #fff;
  position: absolute;
  /* bottom: 10px; */
  transform: rotate(80deg);
}
.times:hover > .time {
  background-color: #114be0;
}

.times:hover > .time::after {
  background-color: #114be0;
}

.bars {
}
@media screen and (max-width: 768px) {
  .admin-nav-container {
    width: 100%;
    margin-right: 6rem;

    justify-content: space-between;
  }

  .admin-links {
    flex-direction: column;
    /* left: -200px; */
    height: 100vh;
    align-items: center;
    z-index: 1000;
  }
  .admin-links .nav-link {
    color: #fff;
  }

  .createExpertLink {
    color: #fff;
  }
  .btn-primary:hover .btn-primary > .createExpertLink {
    color: #114be0;
  }
  .bars {
    display: block;
    margin-right: auto;
  }
  .admin-nav-collection {
    margin-right: 2rem;
  }
  .adminNav {
    position: absolute;
    overflow: hidden;
    height: 100vh;
    background: #7e98da;
    width: 280px;
    left: -500rem;
    z-index: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .admin-links {
    align-self: center;
    margin-top: 50vh;
  }
  .adminNav.active {
    left: -30px;
    top: 0px;
  }
  .adminNav.active > .times {
    display: block;
  }
  .admin-links li:not(:first-child) {
    margin-left: 0px;
    margin-top: 10px;
  }
