.fourthRow {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  flex-direction: row;
}

.form-container form button {
  background: #114be0;
  outline: none;
  color: #fff;
  border: none;
  font-size: 0.9rem;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  align-self: center;
  width: 8rem;
  padding: 10px 20px;
  margin-top: 40px;
}

.form-container form button:hover {
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.1);
  background: #fff;
  color: #114be0;
  border: 2px solid rgba(13, 62, 224, 0.4);
}

nav {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-left: auto;
  display: flex;
  color: #18384c;
}
nav.adminNav {
  margin-left: 0 !important;
  width: 100%;
}

.nav-collection {
  margin-left: auto;
  list-style-type: none;
  display: flex;
  flex-direction: row;
}

.nav-item:not(:last-child) {
  margin-right: 1rem;
}

.nav-item:hover {
  cursor: pointer;
  color: #114be0;
}

#username:hover {
  color: #18384c;
  cursor: auto;
  cursor: initial;
}

